<script>
// @ is an alias to /src
import { swiper, swiperSlide } from "vue-awesome-swiper";
// require styles
import "swiper/dist/css/swiper.css";
import Page3 from "@/business/widgets/slide-page/Page3.vue";
import Page4 from "@/business/widgets/slide-page/Page4.vue";
import Page10 from "@/business/widgets/slide-page/Page10.vue";
import Page11 from "@/business/widgets/slide-page/Page11.vue";
import * as swiperAni from "../static/js/swiper.animate.min";

export default {
  name: "home",
  components: {
    swiper,
    swiperSlide,
    Page3,
    Page4,
    Page10,
    Page11
  },

  data() {
    return {
      swiperOption: {
        // freeMode:true,
        direction: "vertical",
        initialSlide: 0,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        on: {
          init: function() {
            console.log(this);
            swiperAni.swiperAnimateCache(this);
            swiperAni.swiperAnimate(this);
          },

          slideChangeTransitionEnd: function() {
            swiperAni.swiperAnimate(this); //每个slide切换结束时也运行当前slide动画
            //this.slides.eq(this.activeIndex).find('.ani').removeClass('ani'); 动画只展现一次，去除ani类名
          }
        }
      },
      scale: 750 / 1334,
      width: 375,
      height: 667
    };
  },
  created() {
    this.width = document.documentElement.clientWidth;
    this.height = document.documentElement.clientHeight;
  },
  computed: {
    imgStyle() {
      return {
        width: this.height * this.scale + "px",
        height: this.height + "px"
      };
    },
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  mounted() {}
};
</script>

